import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  display: '0',
  expression: '',
  clearOnNumeric: false
};

const performTruncation = (resultString) => {
  for (let i = 0; i < resultString.length; i += 1) {
    if (resultString[i] === '.') {
      return resultString.substring(0, Math.min(i + 5, resultString.length));
    }
  }
  return resultString;
}

const isOperation = (display) => {
  switch (display) {
    case '+':
    case '-':
    case '*':
    case '/':
    case '%':
      return true;
    default:
      return false;
  }
}

const isNumeric = (current) => {
  const firstCharCode = current.charCodeAt(0);
  return firstCharCode >= 48 && firstCharCode <= 57;
}

const isAnswer = (expression) => {
  for (let i = 0; i < expression.length; i += 1) {
    if (expression[i] === '=') {
      return true;
    }
  }
  return false;
}

const hasDecimal = (current) => {
  for (let i = 0; i < current.length; i += 1) {
    if (current[i] === '.') {
      return true;
    }
  }
  return false;
}

const updateExpressionAfterEvaluation = (char, display, expression) => {
  if (isOperation(char)) {
    expression = display + ` ${char}`;
    display = char;
  } else if (isNumeric(char)) {
    expression = char;
    display = char;
  } else {
    if (char === '.') {
      display = '0.';
      expression = display;
    } else if (char === '(') {
      display = '0';
      expression = expression + '(';
    } else if (char === ')') {
      display = '0';
      if (expression[expression.length - 1] === ')') {
        expression = expression.substring(expression.length - 1);
      } else {
        expression = expression + ') ';
      }
    }
  }

  return [
    display,
    expression
  ]
}

const updateExpressionNormally = (char, display, expression) => {
  if (isOperation(char)) {
    if (isOperation(display)) {
      const negated = expression.length > 3 && isOperation(expression[expression.length - 3]) && expression[expression.length - 1] === '-';
      if (negated) {
        if (char !== '-') {
          expression = expression.substring(0, expression.length - 3) + char;
        }
      } else {
        if (char !== '-') {
          expression = expression.substring(0, expression.length - 1) + char;
        } else {
          expression = expression + ` ${char}`;
        }
      }
    } else if (isNumeric(display)) {
      expression = expression + ` ${char}`;
    }
    display = char;
  } else if (isNumeric(char)) {
    if (isOperation(display)) {
      display = char;
      expression = expression + ` ${char}`;
    } else if (isNumeric(display)) {
      if (display === '0' && char !== '0') {
        display = char;
        expression = char;
      } else if (display !== '0' || char !== '0') {
        display = display + char;
        expression = expression + char;
      }
    }
  } else { // Is . or ( or )
    if (char === '.') {
      if (isNumeric(display) && !hasDecimal(display)) {
        display = display + char;
        expression = expression + char;
      } else if (isOperation(display)) {
        display = ' 0.';
        expression = expression + display;
      }
    } else if (char === '(') {
      display = '0';
      expression = expression + '(';
    } else if (char === ')') {
      display = '0';
      if (expression[expression.length - 1] === ')') {
        expression = expression.substring(expression.length - 1);
      } else {
        expression = expression + ') ';
      }
    }
  }
  return [
    display,
    expression
  ]
}

const Expression = createSlice({
  name: 'expression',
  initialState,
  reducers: {
    clearExpression: (state, action) => {
      state.display = '0';
      state.expression = '';
      state.clearOnNumeric = true;
    },
    evaluateExpression: (state, action) => {
      if (state.expression !== '') {
        const expressionEvaluation = performTruncation(String(eval(state.expression)));
        state.display = expressionEvaluation;
        state.expression = `${state.expression} = ${expressionEvaluation}`;
        state.clearOnNumeric = true;
      }
    },
    updateExpression: (state, action) => {
      const char = action.payload;
      const display = state.display;
      const expression = state.expression;

      if (isAnswer(expression)) {
        [state.display, state.expression] = updateExpressionAfterEvaluation(char, display, expression);
      } else {
        [state.display, state.expression] = updateExpressionNormally(char, display, expression);
      }
    }
  }
});

export const { clearExpression, evaluateExpression, updateExpression } = Expression.actions;

export default Expression.reducer;
