import { useDispatch } from "react-redux";
import { updateExpression } from "./Expression";

const BUTTON_HEIGHT = 32;
const BUTTON_WIDTH = 64;

const Button = (props) => {
  const dispatch = useDispatch();
  const char = props.char;
  const onClick = props.onClick || ((event) => {
    event.preventDefault();
    dispatch(updateExpression(char));
  });

  const classList = props.classList || "gray";
  let height;
  const rowSpan = Number(props.rowSpan)
  if (props.rowSpan && rowSpan > 1) {
    height = BUTTON_HEIGHT + (BUTTON_HEIGHT + 4) * (rowSpan - 1);
  } else {
    height = BUTTON_HEIGHT;
  }
  const width = BUTTON_WIDTH;

  return (
    <td rowSpan={rowSpan}>
      <button id={props.id} className={classList} onClick={onClick} style={{ height, width }}>{props.char}</button>
    </td>
  )
}

export default Button;